<template>
  <div id="root">
    <div class="container" ref="scrollableEl">
      <div class="main-block">
        <PlayerAchiv
          :achievementName="achievementName"
          @click="goToLeaderboard"
        />
        <PlayerBal :balCount="balCount" />
        <InfoPanel
          :tapCount="tapCount"
          :tapValue="tapValue"
          :remainingEnergy="remainingEnergy"
          :nextRestoreTime="nextRestoreTime"
        />
      </div>
      <TapArea
        ref="tapArea"
        :tapValue="tapValue"
        :remainingEnergy="remainingEnergy"
        @tap="handleTap"
        @sendTapCountToBackend="sendTapCountToBackend"
        @consumeEnergy="consumeEnergy"
      />
      <ButtonBlock />
    </div>
  </div>
</template>

<script>
import PlayerAchiv from "../components/PlayerAchiv.vue";
import PlayerBal from "../components/PlayerBal.vue";
import InfoPanel from "../components/InfoPanel.vue";
import TapArea from "../components/TapArea.vue";
import ButtonBlock from "../components/ButtonBlock.vue";

export default {
  name: "HomePage",
  components: {
    PlayerAchiv,
    PlayerBal,
    InfoPanel,
    TapArea,
    ButtonBlock,
  },
  data() {
    return {
      tapCount: 0,
      tapValue: 128,
      remainingEnergy: 4 * 256,
      nextRestoreTime: 0,
      balCount: "0",
      achievementName: "Newbie",
      ts: undefined,
      scrollableEl: null,
      restoreTimers: [],
      restoreInterval: null,
    };
  },
  mounted() {
    this.scrollableEl = this.$refs.scrollableEl;

    document.documentElement.addEventListener("touchstart", this.onTouchStart, {
      passive: false,
    });

    document.documentElement.addEventListener("touchmove", this.onTouchMove, {
      passive: false,
    });
  },
  beforeUnmount() {
    document.documentElement.removeEventListener(
      "touchstart",
      this.onTouchStart
    );
    document.documentElement.removeEventListener("touchmove", this.onTouchMove);
    if (this.restoreInterval) {
      clearInterval(this.restoreInterval);
    }
  },
  methods: {
    onTouchStart(e) {
      this.ts = e.touches[0].clientY;
    },
    onTouchMove(e) {
      if (this.scrollableEl) {
        const scroll = this.scrollableEl.scrollTop;
        const te = e.changedTouches[0].clientY;

        if (scroll <= 0 && this.ts < te) {
          e.preventDefault();
        }
      } else {
        e.preventDefault();
      }
    },
    handleTap(value) {
      this.tapCount += value;
    },
    updateTapValue(value) {
      this.tapValue = value;
    },
    updateEnergy(newEnergy) {
      this.remainingEnergy = newEnergy * 256;
      if (newEnergy < 4) {
        this.startRestoreTimer();
      } else {
        this.stopRestoreTimer();
      }
    },
    startRestoreTimer() {
      this.nextRestoreTime = 15000;
      if (!this.restoreInterval) {
        this.restoreInterval = setInterval(() => {
          this.nextRestoreTime -= 1000;
          if (this.nextRestoreTime <= 0) {
            this.nextRestoreTime = 0;
            clearInterval(this.restoreInterval);
            this.restoreInterval = null;
          }
        }, 1000);
      }
    },
    stopRestoreTimer() {
      if (this.restoreInterval) {
        clearInterval(this.restoreInterval);
        this.restoreInterval = null;
      }
      this.nextRestoreTime = 0;
    },
    consumeEnergy() {
      if (this.remainingEnergy > 0) {
        this.remainingEnergy -= 1;
        if (
          this.remainingEnergy % 256 === 0 &&
          this.remainingEnergy < 4 * 256
        ) {
          this.startRestoreTimer();
        }
      }
    },
    sendTapCountToBackend(tapCount) {
      console.log("Tap count sent to backend: ", tapCount);
      // Add the logic to send tapCount to the backend here.
      this.tapCount = 0; // Reset tapCount after sending it to the backend
    },
    updateBalance(balance) {
      this.balCount = balance;
    },
    updateAchievement(achievementName) {
      this.achievementName = achievementName;
    },
    goToLeaderboard() {
      this.$router.push({ name: "LeaderboardPage" });
    },
  },
};
</script>

