<template>
  <div>
    <div
      :class="['leader-item', { 'completed-item': isCompleted }]"
      @click="isCompleted ? null : (showModal = true)"
    >
      <div class="leader-left">
        <img
          :class="['boost-ico', { 'boost-ico-completed': isCompleted }]"
          src="../assets/tgminilogo.svg"
        />
        <div class="lead-info">
          <div class="star-container" ref="starContainer"></div>
          <div :class="['mission-name', { 'text-gray': isCompleted }]">
            Subscribe to TG Channel
          </div>
          <div class="bal-state">
            <img
              :class="['logo-micro', { 'ico-gray': isCompleted }]"
              src="../assets/Gcoinlogo.svg"
            />
            <div :class="['leader-balance', { 'text-gray': isCompleted }]">
              1,024
            </div>
          </div>
        </div>
      </div>

      <div class="leader-right">
        <span v-if="isCompleted" class="completed-text">Completed</span>
        <img v-else src="../assets/arrow.svg" />
      </div>
    </div>

    <transition name="fade">
      <div
        class="modal-overlay"
        v-if="showModal"
        @click="showModal = false"
      ></div>
    </transition>

    <transition name="slide-up">
      <div class="modal" v-if="showModal">
        <div class="modal-content">
          <div class="modal-header">
            <span class="close" @click="showModal = false">
              <img class="close-image" src="../assets/closeButton.svg" />
            </span>
          </div>
          <div class="modal-body">
            <div class="task-info">
              <img class="modal-mis-ico" src="../assets/tgmaxlogo.svg" />
              <div class="task-text">
                <div class="mission-title">
                  <span class="accent">Subscribe to TG Channel</span>
                </div>
                <div class="mission-descr">
                  To complete this mission, subscribe to our Telegram channel
                  and get rewards!
                </div>
              </div>
            </div>
            <button class="button-medium" @click="openLink">Join</button>
            <div class="reward-info">
              <img src="../assets/Gcoinlogo.svg" />
              <div class="reward-display">+2,048</div>
            </div>

            <button
              ref="completeButton"
              :class="[
                'button-custom',
                {
                  'button-enabled': linkClicked,
                  'button-disabled': !linkClicked || isCompleted,
                },
              ]"
              @click="completeMission"
              :disabled="isCompleted || !linkClicked"
            >
              {{ buttonText }}
            </button>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "MissionItem",
  data() {
    return {
      showModal: false,
      linkClicked: false,
      isCompleted: false,
      buttonText: "Complete Mission",
    };
  },
  mounted() {
    this.checkMissionStatus();
  },
  methods: {
    checkMissionStatus() {
      const missionStatus = localStorage.getItem("missionCompleted");
      if (missionStatus === "true") {
        this.isCompleted = true;
        this.buttonText = "Completed";
      }
    },
    completeMission() {
      if (this.linkClicked) {
        this.buttonText = "Completing...";
        setTimeout(() => {
          this.isCompleted = true;
          this.buttonText = "Completed";
          this.triggerStarExplosion();
          this.triggerVibration();
          this.showModal = false;
          localStorage.setItem("missionCompleted", "true");
        }, 1000); // 1-second delay to simulate server response
      }
    },
    openLink() {
      this.linkClicked = true;
      window.open("https://t.me/glyphcoin", "_blank");
    },
    triggerStarExplosion() {
      const buttonRect = this.$refs.completeButton.getBoundingClientRect();
      const starContainer = this.$refs.starContainer;
      const numStars = Math.floor(Math.random() * 21) + 20; // 20 to 40 stars

      for (let i = 0; i < numStars; i++) {
        const star = document.createElement("img");
        star.src = require("../assets/Star.svg");
        star.classList.add("star");
        starContainer.appendChild(star);

        const angle = Math.random() * 2 * Math.PI;
        const distance = Math.random() * 150 + 50;

        star.style.position = "absolute";
        star.style.transform = `translate(${
          buttonRect.left + buttonRect.width / 2 + window.scrollX
        }px, ${buttonRect.top + buttonRect.height / 2 + window.scrollY}px)`;

        star.animate(
          [
            { transform: `translate(0, 0)`, opacity: 1 },
            {
              transform: `translate(${Math.cos(angle) * distance}px, ${
                Math.sin(angle) * distance
              }px)`,
              opacity: 0,
            },
          ],
          {
            duration: 1000,
            easing: "ease-out",
            fill: "forwards",
          }
        );

        setTimeout(() => {
          starContainer.removeChild(star);
        }, 1000);
      }
    },
    triggerVibration() {
      if (
        window.Telegram &&
        window.Telegram.WebApp &&
        window.Telegram.WebApp.HapticFeedback
      ) {
        window.Telegram.WebApp.HapticFeedback.impactOccurred("heavy");
      } else if (navigator.vibrate) {
        navigator.vibrate(200); // Вибрация на 200 миллисекунд с паузой в 100 миллисекунд и снова вибрация на 200 миллисекунд
      }
    },
  },
};
</script>

