<script>
import axios from "axios";

export default {
  name: "ConnectWallet",
  data() {
    return {
      showModalTc: false,
      walletAddress: null, // Моковый адрес кошелька для тестирования
    };
  },
  computed: {
    shortenedWalletAddress() {
      if (!this.walletAddress) return "";
      return `${this.walletAddress.slice(0, 3)}...${this.walletAddress.slice(
        -3
      )}`;
    },
  },
  mounted() {
    // Закомментирован вызов функции для загрузки кошелька
    this.fetchWalletAddress();
  },
  methods: {
    async fetchWalletAddress() {
      try {
        // Используем mock данные для тестирования
        const mockResponse = {
          data: {
            walletAddress: "UQAg8QWWYPlRdyhXO_6zWDc7DPqYqBXwuspbqNOhC_EpFQP0",
          },
        };
        this.walletAddress = mockResponse.data.walletAddress;
        this.$emit("wallet-connected", !!this.walletAddress);

        // Реальный запрос на API (раскомментируйте для использования)
        // const response = await axios.get(`test/api/v1/users/${userId}/wallet`);
        // this.walletAddress = response.data.walletAddress;
        // this.$emit('wallet-connected', !!this.walletAddress);
      } catch (error) {
        console.error("Ошибка при получении адреса кошелька:", error);
      }
    },
    async disconnectWallet() {
      try {
        // Моковый userId для тестирования
        const userId = "12345";
        await axios.delete(`test/api/v1/users/${userId}/wallet_connection`);
        this.walletAddress = null; // Обновляем состояние после отключения кошелька
        this.$emit("wallet-connected", false);
      } catch (error) {
        console.error("Ошибка при отключении кошелька:", error);
      }
    },
  },
};
</script>

