<template>
  <div class="container-boosts">
    <div class="page-title">Default Boosts</div>
    <DefaultBoost />
    <div class="page-title">Your Wallet</div>
    <ConnectWallet @wallet-connected="handleWalletConnected" />
    <div class="page-title">NFT-Boosts</div>
    <div class="NFT-container">
      <div class="NFT-grid">
        <NftBoost
          v-for="(nft, index) in nftBoosts"
          :key="index"
          :icon="nft.icon"
          :name="nft.name"
        />
      </div>
    </div>
    <div class="page-title">Missions</div>
    <div class="mis-container">
      <MissionItem4 />
      <MissionItem />
      <MissionItem2 :isWalletConnected="isWalletConnected" />
      <MissionItem3 :hasNftBoosts="hasNftBoosts" />
    </div>
    <div class="debug-field">
      <button class="button-large" @click="clearLocalStorage">
        Clear Local Storage (Debug)
      </button>
    </div>
  </div>
</template>

<script>
import DefaultBoost from "../components/DefaultBoost.vue";
import ConnectWallet from "../components/ConnectWallet.vue";
import MissionItem from "../components/MissionItem.vue";
import MissionItem2 from "../components/MissionItem2.vue";
import MissionItem3 from "../components/MissionItem3.vue";
import MissionItem4 from "../components/MissionItem4.vue";
import NftBoost from "../components/NftBoost.vue";
// import axios from 'axios'; // Оставим закомментированным, если потребуется для реальных запросов

export default {
  name: "BoostPage",
  components: {
    DefaultBoost,
    ConnectWallet,
    MissionItem,
    MissionItem2,
    MissionItem3,
    MissionItem4,
    NftBoost,
  },
  data() {
    return {
      nftBoosts: [],
      minSlots: 3,
      isWalletConnected: false, // Добавляем новое состояние
    };
  },
  computed: {
    hasNftBoosts() {
      return this.nftBoosts.some((nft) => nft.name !== "Empty");
    },
  },
  mounted() {
    if (window.Telegram && window.Telegram.WebApp) {
      window.Telegram.WebApp.BackButton.show();
      window.Telegram.WebApp.BackButton.onClick(this.goBack);
    }

    this.fetchNftBoosts();
  },
  beforeUnmount() {
    if (window.Telegram && window.Telegram.WebApp) {
      window.Telegram.WebApp.BackButton.hide();
      window.Telegram.WebApp.BackButton.offClick(this.goBack);
    }
  },
  methods: {
    handleWalletConnected(isConnected) {
      this.isWalletConnected = isConnected;
    },
    clearLocalStorage() {
      // Очистка всего local storage
      localStorage.clear();
      // Если нужно удалить конкретный элемент:
      // localStorage.removeItem('key');
      console.log("Local storage очищен");
    },
    goBack() {
      this.$router.go(-1);
    },
    async fetchNftBoosts() {
      try {
        // Имитируем задержку запроса с использованием setTimeout
        setTimeout(() => {
          // Моковые данные, имитирующие ответ от сервера
          const response = {
            data: [
              { name: "Tap Reward +8" },
              { name: "Auto Farm 128 GLYPH/s" },
              { name: "Tap Reward +32" },
            ],
          };

          const nfts = response.data;

          const allNfts = [
            { name: "Tap Reward +8", icon: "TapReward8.svg" },
            { name: "Tap Reward +16", icon: "TapReward16.svg" },
            { name: "Tap Reward +32", icon: "TapReward32.svg" },
            { name: "Tap Reward +64", icon: "TapReward64.svg" },
            { name: "Tap Reward +128", icon: "TapReward128.svg" },
            { name: "Tap Reward +256", icon: "TapReward256.svg" },
            { name: "Tap Reward +512", icon: "TapReward512.svg" },
            { name: "Tap Reward +1024", icon: "TapReward1024.svg" },
            { name: "Auto Farm 128 GLYPH/s", icon: "AutoFarm128.svg" },
            { name: "Auto Farm 256 GLYPH/s", icon: "AutoFarm256.svg" },
            { name: "Auto Farm 512 GLYPH/s", icon: "AutoFarm512.svg" },
            { name: "Auto Farm 1024 GLYPH/s", icon: "AutoFarm1024.svg" },
            { name: "Auto Farm 2048 GLYPH/s", icon: "AutoFarm2048.svg" },
            { name: "Auto Farm 4096 GLYPH/s", icon: "AutoFarm4096.svg" },
            { name: "Auto Farm 8192 GLYPH/s", icon: "AutoFarm8192.svg" },
            { name: "Auto Farm 16384 GLYPH/s", icon: "AutoFarm16384.svg" },
          ];

          this.nftBoosts = allNfts
            .map((nft) => {
              const userNft = nfts.find((userNft) => userNft.name === nft.name);
              return userNft ? { name: userNft.name, icon: nft.icon } : null;
            })
            .filter((nft) => nft !== null);

          // Заполнение пустых слотов
          let currentSlots = this.minSlots;
          while (this.nftBoosts.length >= currentSlots) {
            currentSlots += this.minSlots;
          }
          while (this.nftBoosts.length < currentSlots) {
            this.nftBoosts.push({ name: "Empty", icon: null });
          }
        }, 10); // Имитируем задержку в 1 секунду
      } catch (error) {
        console.error("Ошибка при получении NFT бустов:", error);
        // Заполнение пустых слотов, если произошла ошибка при получении данных
        let currentSlots = this.minSlots;
        while (this.nftBoosts.length >= currentSlots) {
          currentSlots += this.minSlots;
        }
        while (this.nftBoosts.length < currentSlots) {
          this.nftBoosts.push({ name: "Empty", icon: null });
        }
      }
    },
  },
};
</script>

