  display: flex;
  flex-direction: column;
  gap: 10px;
  margin: 0 auto;
  padding-top: 10px;
  max-width: 768px;
  width: 100%;
}
.page-title {
  color: white;
  font-size: 12px;
  font-weight: 400;
  padding-left: 20px;
}

.NFT-container {
  display: flex;
  justify-content: center;
  width: 100%;
  background-color: #111111;
  padding: 20px;
}

.NFT-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
}

.tc-root {
  all: unset;
}

.debug-field {
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
