<template>
  <div class="test-item" @click="handleClick">
    <div class="item-pic">
      <img v-if="icon" :src="iconPath" class="NFT" />
      <img v-else src="../assets/Plus.svg" />
    </div>
    <div :class="itemNameClass">{{ name }}</div>
  </div>
</template>

<script>
export default {
  name: "NftBoost",
  props: {
    icon: {
      type: String,
      default: null,
    },
    name: {
      type: String,
      default: "Empty",
    },
  },
  computed: {
    iconPath() {
      return this.icon ? require(`../assets/${this.icon}`) : null;
    },
    itemNameClass() {
      return this.icon ? "item-name-filled" : "item-name-empty";
    },
  },
  methods: {
    handleClick() {
      if (!this.icon) {
        window.open("https://getgems.io/glyph-boosts", "_blank");
      }
    },
  },
};
</script>

<style>
.test-item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  cursor: pointer; /* Add a pointer cursor to indicate it's clickable */
}

.item-pic {
  width: 110px;
  height: 110px;
  border: dashed 1px #3f3f3f;
  border-radius: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.NFT {
  width: 110px;
  height: 110px;
  object-fit: cover;
}

.item-name-empty {
  font-size: 12px;
  font-weight: 400;
  color: #3f3f3f;
  text-align: center;
}

.item-name-filled {
  font-size: 12px;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
}

@media (max-width: 390px) {
  .item-pic {
    width: 30vw;
    height: 30vw;
    max-width: 110px;
    max-height: 110px;
  }
  .NFT {
    width: 30vw;
    height: 30vw;
    max-width: 110px;
    max-height: 110px;
  }
}
</style>
