<template>
  <div class="link-item">
    <div class="leader-left">
      <img class="boost-ico" src="../assets/link_ico.svg" />
      <div class="lead-info">
        <div class="ref-link">{{ fullReferralLink }}</div>
      </div>
    </div>
    <div class="leader-right">
      <button class="button-medium" @click="shareLink">Share</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "RefLink",
  data() {
    return {
      userId: "1488", // Часть ссылки, полученная с бэкенда
      baseReferralLink: "http://t.me/maslachussetsbot/glyphtest/startapp=r",
      referralText:
        "Join me in becoming a cryptocurrency miner! Earn GLYPH coins just by tapping on your smartphone screen! Check out the high-yield NFT boosts before they're all sold out!", // Текст для пересылки
    };
  },
  computed: {
    fullReferralLink() {
      return `${this.baseReferralLink}${this.userId}`;
    },
  },
  mounted() {
    this.fetchReferralLink();
  },
  methods: {
    async fetchReferralLink() {
      try {
        const response = await axios.get(
          "https://api.example.com/referral-link"
        ); // Замените URL на ваш эндпоинт
        this.userId = response.data.referralLink;
      } catch (error) {
        console.error("Ошибка при получении реферальной ссылки:", error);
      }
    },
    shareLink() {
      if (window.Telegram && window.Telegram.WebApp) {
        window.Telegram.WebApp.openTelegramLink(
          `https://t.me/share/url?url=${encodeURIComponent(
            this.fullReferralLink
          )}&text=${encodeURIComponent(this.referralText)}`
        );
      } else {
        console.error("Telegram WebApp is not available");
      }
    },
  },
};
</script>

<style scoped>
.link-item {
  width: 100%;
  background-color: #111111;
  display: flex;
  justify-content: space-between;
  align-items: center;
