<template>
  <div
    ref="tapContainer"
    class="tap-container"
    @touchstart="handleTouchStart"
    @touchend="handleTouchEnd"
    @touchcancel="handleTouchEnd"
  >
    <div class="internal-content">
      <div class="tap-navigation">
        <img class="tap-logo" src="../assets/gph_logo.svg" />
        <div class="tap-txt">Mining area</div>
      </div>
    </div>
    <div
      class="gradient"
      v-for="gradient in gradients"
      :key="gradient.id"
      :style="gradient.style"
    ></div>
  </div>
</template>

<script>
export default {
  name: "TapArea",
  props: {
    tapValue: {
      type: Number,
      default: 1,
    },
    remainingEnergy: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      activeTouches: 0,
      plusOnes: [],
      gradients: [],
      tapCount: 0,
      totalTaps: 0, // Новый счетчик для общего числа тапов
      cleanupFunctions: [], // Массив для хранения функций очистки
    };
  },
  methods: {
    handleTouchStart(event) {
      if (this.remainingEnergy <= 0) {
        return; // Не обрабатываем тап, если нет энергии
      }
      this.activeTouches += event.changedTouches.length;
      this.tapCount += event.changedTouches.length;
      this.totalTaps += event.changedTouches.length; // Увеличиваем общий счетчик тапов
      this.showPlusOne(event.changedTouches);
      this.showGradient(event.changedTouches);
      this.emitTapEvent();
      this.triggerVibration();
      this.consumeEnergy();

      if (this.tapCount >= 256) {
        this.tapCount = 0; // Сброс счетчика тапов
        this.$emit("sendTapCountToBackend", this.tapCount);
      }

      if (this.totalTaps >= 256) {
        this.totalTaps = 0; // Сброс общего счетчика тапов
        this.$emit("sendTapCountToBackend"); // Отправка события на бэкенд
      }
    },
    handleTouchEnd(event) {
      this.activeTouches -= event.changedTouches.length;
      if (this.activeTouches < 0) {
        this.activeTouches = 0;
      }
    },
    showPlusOne(touches) {
      const rect = this.$refs.tapContainer.getBoundingClientRect();
      Array.from(touches).forEach((touch) => {
        const x = touch.clientX - rect.left;
        const y = touch.clientY - rect.top;
        const id = Date.now() + Math.random(); // Уникальный идентификатор для каждого элемента
        const plusOne = {
          id,
          style: {
            position: "absolute",
            left: `${x}px`,
            top: `${y}px`,
            opacity: 1,
          },
        };
        this.plusOnes.push(plusOne);
        const plusOneElement = document.createElement("div");
        plusOneElement.textContent = `+${this.tapValue}`;
        Object.assign(plusOneElement.style, plusOne.style, {
          fontSize: "24px",
          fontWeight: "bold",
          color: "white",
          pointerEvents: "none",
          userSelect: "none",
          position: "absolute",
        });
        if (this.$refs.tapContainer) {
          this.$refs.tapContainer.appendChild(plusOneElement);
        }
        const animation = plusOneElement.animate(
          [
            { opacity: 1, transform: "translateY(0)" },
            { opacity: 0, transform: "translateY(-100px)" },
          ],
          {
            duration: 1000,
            easing: "ease-out",
            fill: "forwards",
          }
        );
        const timeoutId = setTimeout(() => {
          this.plusOnes = this.plusOnes.filter((p) => p.id !== id);
          if (this.$refs.tapContainer && plusOneElement.parentElement) {
            this.$refs.tapContainer.removeChild(plusOneElement);
          }
        }, 1000);

        this.cleanupFunctions.push(() => {
          clearTimeout(timeoutId);
          animation.cancel();
          if (this.$refs.tapContainer && plusOneElement.parentElement) {
            this.$refs.tapContainer.removeChild(plusOneElement);
          }
        });
      });
    },
    showGradient(touches) {
      const rect = this.$refs.tapContainer.getBoundingClientRect();
      Array.from(touches).forEach((touch) => {
        const x = touch.clientX - rect.left;
        const y = touch.clientY - rect.top;
        const id = Date.now() + Math.random(); // Уникальный идентификатор для каждого элемента
        const color = `hsl(${Math.random() * 360}, 100%, 75%)`; // Рандомный цвет
        const gradient = {
          id,
          style: {
            position: "absolute",
            left: `${x - 50}px`,
            top: `${y - 50}px`,
            width: "100px",
            height: "100px",
            background: `radial-gradient(circle, ${color} 0%, transparent 70%)`,
            opacity: 0,
          },
        };
        this.gradients.push(gradient);
        const gradientElement = document.createElement("div");
        Object.assign(gradientElement.style, gradient.style, {
          borderRadius: "50%",
          pointerEvents: "none",
          userSelect: "none",
        });
        if (this.$refs.tapContainer) {
          this.$refs.tapContainer.appendChild(gradientElement);
        }
        const animation = gradientElement.animate(
          [
            { opacity: 1, transform: "scale(0.5)" },
            { opacity: 0, transform: "scale(1.5)" },
          ],
          {
            duration: 1000,
            easing: "ease-out",
            fill: "forwards",
          }
        );
        const timeoutId = setTimeout(() => {
          this.gradients = this.gradients.filter((g) => g.id !== id);
          if (this.$refs.tapContainer && gradientElement.parentElement) {
            this.$refs.tapContainer.removeChild(gradientElement);
          }
        }, 1000);

        this.cleanupFunctions.push(() => {
          clearTimeout(timeoutId);
          animation.cancel();
          if (this.$refs.tapContainer && gradientElement.parentElement) {
            this.$refs.tapContainer.removeChild(gradientElement);
          }
        });
      });
    },
    emitTapEvent() {
      this.$emit("tap", this.tapValue);
    },
    triggerVibration() {
      if (
        window.Telegram &&
        window.Telegram.WebApp &&
        window.Telegram.WebApp.HapticFeedback
      ) {
        window.Telegram.WebApp.HapticFeedback.impactOccurred("medium");
      } else if (navigator.vibrate) {
        navigator.vibrate(200); // Вибрация на 200 миллисекунд
      }
    },
    consumeEnergy() {
      this.$emit("consumeEnergy");
    },
    showStarExplosion(touches) {
      const rect = this.$refs.tapContainer.getBoundingClientRect();
      Array.from(touches).forEach((touch) => {
        const x = touch.clientX - rect.left;
        const y = touch.clientY - rect.top;

        const starCount = Math.floor(Math.random() * 21) + 60;
        for (let i = 0; i < starCount; i++) {
          const starElement = document.createElement("img");
          starElement.src = require("../assets/Star.svg");
          starElement.style.position = "absolute";
          starElement.style.left = `${x}px`;
          starElement.style.top = `${y}px`;
          starElement.style.width = "20px";
          starElement.style.height = "20px";
          starElement.style.pointerEvents = "none";
          starElement.style.userSelect = "none";
          if (this.$refs.tapContainer) {
            this.$refs.tapContainer.appendChild(starElement);
          }

          const angle = Math.random() * 2 * Math.PI;
          const radius = Math.random() * 100 + 50; // Радиус разлета от 50 до 150 пикселей
          const targetX = x + radius * Math.cos(angle);
          const targetY = y + radius * Math.sin(angle);

          const animation = starElement.animate(
            [
              { transform: "translate(0, 0)", opacity: 1 },
              {
                transform: `translate(${targetX - x}px, ${targetY - y}px)`,
                opacity: 0,
              },
            ],
            {
              duration: 1000,
              easing: "ease-out",
              fill: "forwards",
            }
          );

          const timeoutId = setTimeout(() => {
            if (this.$refs.tapContainer && starElement.parentElement) {
              this.$refs.tapContainer.removeChild(starElement);
            }
          }, 1000);

          this.cleanupFunctions.push(() => {
            clearTimeout(timeoutId);
            animation.cancel();
            if (this.$refs.tapContainer && starElement.parentElement) {
              this.$refs.tapContainer.removeChild(starElement);
            }
          });
        }
      });
    },
  },
  beforeUnmount() {
    // Очищаем все отложенные действия
    this.cleanupFunctions.forEach((cleanup) => cleanup());
  },
};
</script>

<style scoped>
.tap-container {
  position: relative;
  width: 100%;
  height: 290px;
  touch-action: none;
  border: dashed 1px #3f3f3f;
  border-radius: 16px;
  overflow: hidden;
}

.internal-content {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tap-navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.tap-logo {
  width: 64px;
  height: 64px;
}

.tap-txt {
  font-size: 12px;
  font-weight: 400;
  color: #3f3f3f;
  text-align: center;
}
</style>
